:root {
  --flex-item: 5;
  --flex-item-gap: 20px;
  --main-text-color: #111;
  --main-text-color-60: rgba(17, 17, 17, .6);
  --extra-text-color: #4f2ee8;
  --sub-text-color: #b4afaf;
  --light-text-color: #fff;
  --light-text-color-60: rgba(255, 255, 255, .6);
  --place-text-color: #eac645;
  --dark-text-color-60: rgba(0, 0, 0, .6);
  --main-bg-color: #111;
  --extra-bg-color: #4f2ee8;
  --place-bg-color: #eac645;
  --bg-color: #f6f6f6;
  --extra-border-color: #4f2ee8;
  --extra-border-color-40: rgba(79, 46, 232, .4);
  --empty: #fff;
  --transition-property: color, background, box-shadow;
  --transition-duration: .3s;
  --transition-timing-function: ease-out;
}

*, :before, :after {
  box-sizing: border-box;
}

body {
  color: var(--main-text-color);
  letter-spacing: -.04em;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28571;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 375px) {
  .container {
    max-width: 375px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding: 0 24px;
  }
}

.global-title, .global-p {
  margin: 0;
}

.global-link {
  color: currentColor;
  text-decoration: none;
}

.global-button {
  color: currentColor;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
  letter-spacing: inherit;
  cursor: pointer;
  border: none;
}

.global-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.flex-container {
  gap: var(--flex-item-gap);
  flex-wrap: wrap;
  display: flex;
}

.flex-container-item {
  flex-basis: calc( (100% - (var(--flex-item)  - 1) * var(--flex-item-gap) ) / var(--flex-item) );
}

.main-block > .container {
  flex-direction: column;
  gap: 40px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .main-block > .container {
    gap: 100px;
  }
}

@media screen and (min-width: 1440px) {
  .main-block > .container {
    flex-direction: row;
    gap: 40px;
  }
}

.asides {
  flex-direction: column;
  gap: 40px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .asides {
    gap: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .asides {
    flex: auto;
    gap: 86px;
  }
}



.support-aside {
  width: 335px;
  height: 352px;
  background: radial-gradient( ellipse at top, var(--extra-bg-color) 18.03%, var(--light-text-color) 100% );
  border-radius: 10px;
  box-shadow: 0 10px 10px rgba(245, 245, 247, .1);
}

.support-aside, .support-item a {
  color: var(--light-text-color);
}

.support-list {
  padding-left: 0;
}

.support-title {
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
  padding-top: 24px;
  font-size: 28px;
  font-weight: 700;
  line-height: 1.14;
  display: flex;
}

.support-title img {
  margin-left: 12px;
}

.support-item {
  align-items: center;
  gap: 14px;
  margin-bottom: 14px;
  margin-left: 40px;
  display: flex;
}

.item-number {
  line-height: 1.29;
}

@media screen and (min-width: 376px) and (max-width: 767px) {
  .support-aside {
    height: 472px;
  }
}

@media screen and (min-width: 768px) {
  .support-aside {
    width: 356px;
    height: 490px;
  }
}





